@media (min-width: 600px) {
  :root {
    --gradient: linear-gradient(
            45deg,
            #019ed3,
            #383838
        )
    ; } }

@media (max-width: 599px) {
  :root {
    --gradient: linear-gradient(
            180deg,
            #019ed3,
            #383838
        )
    ; } }

@font-face {
  font-family: "Open Sans";
  src: url("../fonts/open-sans/OpenSans-Regular.ttf") format("TrueType"); }

@font-face {
  font-family: "Open Sans Bold";
  src: url("../fonts/open-sans/OpenSans-Bold.ttf") format("TrueType"); }

* {
  margin: 0;
  padding: 0; }

h1, h2, h3, h4, h5, h6, p {
  opacity: 0.9; }

html, body {
  display: flex;
  width: 100%;
  height: 100%;
  font-family: "Open Sans";
  cursor: default;
  scroll-behavior: smooth; }

strong {
  font-weight: 800; }

.motherDiv {
  background: #02536D;
  height: 100%;
  min-height: 100%;
  overflow-y: scroll; }
  .motherDiv::-webkit-scrollbar {
    width: 0px;
    height: 0px;
    background: transparent; }
  .motherDiv .mainDiv {
    width: 100%;
    height: calc(100% - 50px); }
    .motherDiv .mainDiv .introSegment {
      display: flex;
      justify-content: center;
      align-items: center;
      height: 100%;
      padding-left: 10%;
      padding-right: 10%;
      background: #019ed3; }
      .motherDiv .mainDiv .introSegment .introP {
        width: inherit;
        font-size: 3rem;
        align-self: center;
        color: #d34301;
        color: #282828;
        font-family: "Open Sans Bold";
        animation: name-animate 7s infinite alternate;
        animation-delay: 1s;
        font-weight: 900; }

@keyframes name-animate {
  from {
    color: #282828; }
  to {
    color: #d34301; } }
        .motherDiv .mainDiv .introSegment .introP::before {
          font-family: "Open Sans";
          content: 'My name is ';
          color: #ffffff; }
        .motherDiv .mainDiv .introSegment .introP::after {
          font-family: "Open Sans";
          content: ' & I create great experiences on the internet.';
          color: #ffffff; }
        @media (max-width: 1065px) {
          .motherDiv .mainDiv .introSegment .introP {
            font-size: 2.6rem; } }
        @media (max-width: 650px) {
          .motherDiv .mainDiv .introSegment .introP {
            font-size: 2rem; } }
        @media (max-width: 530px) {
          .motherDiv .mainDiv .introSegment .introP {
            font-size: 1.8rem; } }
    .motherDiv .mainDiv .workSegment {
      display: flex;
      justify-content: center;
      align-items: center;
      flex-direction: column;
      height: 37vw;
      color: #ffffff;
      position: relative;
      background: #383838;
      padding: 150px;
      padding-left: 0;
      padding-right: 0; }
      .motherDiv .mainDiv .workSegment .textDiv {
        position: absolute;
        top: 100px;
        width: 65vw;
        max-width: 750px; }
        .motherDiv .mainDiv .workSegment .textDiv .workHeadDiv {
          margin-bottom: 10px; }
          .motherDiv .mainDiv .workSegment .textDiv .workHeadDiv .workHead {
            font-size: 3rem; }
        .motherDiv .mainDiv .workSegment .textDiv .workParaDiv {
          margin-bottom: 40px; }
          .motherDiv .mainDiv .workSegment .textDiv .workParaDiv .workPara {
            font-size: 1.3rem; }
      .motherDiv .mainDiv .workSegment .workDiv {
        display: flex;
        justify-content: center;
        align-items: center;
        width: 100%;
        position: absolute;
        top: 300px; }
        .motherDiv .mainDiv .workSegment .workDiv .workSubDiv {
          padding-top: 30px;
          padding-bottom: 30px;
          height: 100%;
          width: 100%;
          display: grid;
          grid-template-columns: repeat(3, 30vw);
          grid-template-rows: auto;
          justify-items: center;
          justify-content: center;
          grid-gap: 2%; }
          .motherDiv .mainDiv .workSegment .workDiv .workSubDiv .workExample {
            display: flex;
            justify-content: center;
            align-items: center;
            align-self: center;
            width: 100%;
            height: 20vw;
            background: #019ed3;
            border-radius: 5px;
            box-shadow: 0 5px 15px rgba(0, 0, 0, 0.7);
            transition: all .35s ease-in-out; }
            .motherDiv .mainDiv .workSegment .workDiv .workSubDiv .workExample h1 {
              font-size: calc(3vw + 0.5rem); }
            .motherDiv .mainDiv .workSegment .workDiv .workSubDiv .workExample:hover {
              filter: brightness(120%);
              cursor: pointer;
              box-shadow: 0 6px 17px rgba(0, 0, 0, 0.7); }
      @media (max-width: 900px) {
        .motherDiv .mainDiv .workSegment {
          height: 80vh; }
          .motherDiv .mainDiv .workSegment .textDiv {
            width: 70%; }
            .motherDiv .mainDiv .workSegment .textDiv .workParaDiv {
              width: 100%; }
          .motherDiv .mainDiv .workSegment .workDiv .workSubDiv {
            grid-template-columns: repeat(2, 46vw);
            grid-template-rows: auto auto;
            row-gap: 3vw; }
            .motherDiv .mainDiv .workSegment .workDiv .workSubDiv .workExample {
              width: 45vw;
              height: 24vw; }
              .motherDiv .mainDiv .workSegment .workDiv .workSubDiv .workExample h1 {
                font-size: calc(3vw + 1rem); } }
      @media (max-width: 850px) {
        .motherDiv .mainDiv .workSegment {
          height: 65vh; } }
      @media (max-width: 740px) {
        .motherDiv .mainDiv .workSegment .textDiv .workParaDiv .workPara {
          font-size: 1.2rem; } }
      @media (max-width: 550px) {
        .motherDiv .mainDiv .workSegment {
          height: 95vh;
          padding-bottom: 150px; }
          .motherDiv .mainDiv .workSegment .textDiv {
            width: 75vw;
            align-self: center; }
            .motherDiv .mainDiv .workSegment .textDiv .workParaDiv {
              width: 100%; }
              .motherDiv .mainDiv .workSegment .textDiv .workParaDiv .workPara {
                font-size: 1.1rem;
                width: 100%; }
          .motherDiv .mainDiv .workSegment .workDiv .workSubDiv {
            grid-template-columns: repeat(1, 1fr);
            grid-template-rows: auto auto auto;
            row-gap: 3vh; }
            .motherDiv .mainDiv .workSegment .workDiv .workSubDiv .workExample {
              width: 45vh;
              height: 25vh; }
              .motherDiv .mainDiv .workSegment .workDiv .workSubDiv .workExample h1 {
                font-size: calc(3vw + 1.3rem); } }
      @media (max-width: 450px) {
        .motherDiv .mainDiv .workSegment .workParaDiv {
          width: 40vh; }
        .motherDiv .mainDiv .workSegment .workDiv .workSubDiv .workExample {
          width: 40vh; } }
